<template>
  <div class="city-container w-100">
    <div class="city-main bg-white">
      <!-- 组件化开发 跨组件共享数据 用vuex处理 -->
      <ChangeCity
          title="按省份选择:"
          theme="省份"
          subTheme="城市"
          searchTheme="直接搜索："
        ></ChangeCity>
      <List :listItems="hotCitys" title="热门城市:"></List>
      <List :listItems="reqCitys" title="最近访问:"></List>
      <ListWord title="按拼音首字母选择：" :words="words"></ListWord>
      <div v-loading="CityInfo['A'].length === 0">
        <WordCity
          v-show="CityInfo[word].length > 0"
          v-for="(word, i) in words"
          :key="`${word}${i}`"
          :cityItems="CityInfo[word]"
          :word="word"
        ></WordCity>
      </div>
    </div>
  </div>
</template>

<script>
import ChangeCity from "@/components/ChangeCity";
import List from "@/components/List";
import ListWord from "@/components/ListWord";
import WordCity from "@/components/WordCity";
import { mapState } from "vuex";
export default {
  components: {
    ChangeCity,
    List,
    ListWord,
    WordCity,
  },
  computed: {
    ...mapState("cityStore", ["hotCitys", "reqCitys", "allCity"]),
  },
  data() {
    return {
      words: [
        "A",
        "B",
        "C",
        "D",
        "E",
        "F",
        "G",
        "H",
        "J",
        "K",
        "L",
        "M",
        "N",
        "P",
        "Q",
        "R",
        "S",
        "T",
        "W",
        "X",
        "Y",
        "Z",
      ],
      CityInfo: {
        A: [],
        B: [],
        C: [],
        D: [],
        E: [],
        F: [],
        G: [],
        H: [],
        J: [],
        K: [],
        L: [],
        M: [],
        N: [],
        P: [],
        Q: [],
        R: [],
        S: [],
        T: [],
        W: [],
        X: [],
        Y: [],
        Z: [],
      },
    };
  },
  async created() {
    this.$store.dispatch("cityStore/asyFetchHotCity");
    this.$store.dispatch("cityStore/asyFetchReqCitys");
    await this.$store.dispatch("cityStore/asyFetchAllCity");
    await new Promise((resolve) => {
      setTimeout(() => {
        resolve();
      }, 1000);
    });
    this.words.forEach((word) => {
      this.CityInfo[word] = this.computedCityByWord(word);
    });
  },
  methods: {
    computedCityByWord(word) {
      var arr = [];
      for (let i = 0; i < this.allCity.length; i++) {
        let temp = this.allCity[i].cityInfoList.filter(
          (ele) => ele.firstChar == word
        );
        arr = arr.concat(temp);
      }
      return arr;
    },
  },
};
</script>

<style lang="less" scoped>
.city-container {
  .city-main {
    width: 1190px;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    min-height: 1000px;
    padding: 20px;
    margin: 20px auto 0;
  }
}
</style>