<template>
  <dl class="d-flex my-dl">
    <dt>{{ title }}</dt>
    <dd class="theme position-relative">
      <div class="theme-item" @click.stop="showProvince">
        {{ selProvince !== "" ? selProvince : theme
        }}<i class="el-icon-caret-bottom float-right mr-3"></i>
      </div>
      <ShowWrapper
        @show="isShowProvince = $event"
        v-show="isShowProvince"
        title="省份"
        :dataList="computedProvince"
      ></ShowWrapper>
    </dd>
    <dd class="subTheme position-relative">
      <div
        class="theme-item"
        :class="{
          disabled: selProvince === '',
        }"
        @click.stop="handleClick"
      >
        {{ subTheme }} <i class="el-icon-caret-bottom float-right mr-3"></i>
      </div>
      <ShowCity
        title="城市"
        :dataList="computedCity.cityInfoList"
        v-show="isCityShow && selProvince !== ''"
      ></ShowCity>
    </dd>
    <dd class="searchTheme">{{ searchTheme }}</dd>
    <dd
      class="position-relative search-box"
      :class="{
        active: hotWord !== '',
      }"
    >
      <el-input v-model="hotWord" :placeholder="placeHolder"></el-input>
      <SearchMenu v-if="hotWord !== ''" :keyWord="hotWord"></SearchMenu>
    </dd>
  </dl>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import ShowWrapper from "./ShowWrapper";
import ShowCity from "./ShowCity";
import SearchMenu from "./SearchMenu";
export default {
  components: {
    ShowWrapper,
    ShowCity,
    SearchMenu,
  },
  props: {
    title: String,
    theme: String,
    subTheme: String,
    searchTheme: String,
    placeHolder: {
      type: String,
      default: "请输入城市中文或拼音",
    },
  },
  data() {
    return {
      hotWord: "",
      isShowProvince: false,
      isCityShow: false,
    };
  },
  computed: {
    ...mapGetters("cityStore", ["computedProvince", "computedCity"]),
    ...mapState("cityStore", ["selCitys", "selProvince"]),
  },
  created() {
    document.addEventListener("click", () => {
      this.isShowProvince = false;
      this.isCityShow = false;
    });
  },
  methods: {
    async showProvince() {
      this.isCityShow = false;
      this.$store.dispatch("cityStore/asyFetchAllCity"); // 不大好 最好是外面传个函数进来 可以让组件的扩展性增强
      this.isShowProvince = true;
    },
    handleClick() {
      this.isShowProvince = false;
      this.selProvince !== "" && (this.isCityShow = true);
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/css/common/var.less";
.my-dl {
  box-sizing: content-box;
  padding-bottom: 30px;
  border-bottom: 1px solid #e5e5e5;
  height: 40px;
  line-height: 40px;
  dt {
    font-size: 16px;
    color: #333;
    font-weight: 500;
  }
  .searchTheme {
    margin-left: 40px;
  }
  .theme,
  .subTheme {
    .theme-item {
      line-height: initial !important;
      margin: 0 10px 0 20px;
      padding: 10px 0 10px 10px;
      width: 150px;
      height: 40px;
      border: 1px solid #e5e5e5;
      cursor: pointer;
      border-radius: 4px;
      vertical-align: middle;
      font-size: 14px;
      color: #666;
      line-height: 40px;
      &.disabled {
        cursor: not-allowed;
      }
    }
  }
  /deep/ .el-input__inner:focus {
    border-color: @green;
  }
}
.search-box {
  &.active::before {
    content: "";
    position: absolute;
    left: 26px;
    top: calc(100% + 2.7px);
    transform: rotate(45deg);
    width: 8px;
    z-index: 100;
    height: 8px;
    background-color: #fff;
    border-left: 1px solid #e5e5e5;
    border-top: 1px solid #e5e5e5;
  }
}
</style>