<template>
  <div class="search-menu">
    <a href="#" v-for="item in SearchByKeyWord(keyWord)" :key="item.id">{{
      item.name
    }}</a>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: {
    keyWord: String,
  },
  computed: {
    ...mapState("cityStore", ["allCity"]),
  },
  methods: {
    SearchByKeyWord(key) {
      let targetArr = [];
      let allCity = this.allCity;
      for (let i = 0; i < allCity.length; i++) {
        var temp = allCity[i].cityInfoList.filter((ele) => {
          return (
            ele.acronym.includes(key) ||
            ele.pinyin.includes(key) ||
            ele.name.includes(key)
          );
        });
        targetArr = targetArr.concat(temp);
      }
      return targetArr;
    },
  },
};
</script>

<style lang="less" scoped>
.search-menu {
  position: absolute;
  overflow-y: auto;
  min-width: 150px;
  max-height: 375px;
  padding: 0 20px 0 15px;
  background-color: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 4px;
  top: calc(100% + 6px);
  box-shadow: 0 3px 5px 0 rgb(0 0 0 / 10%);

  a {
    cursor: pointer;
    font-size: 12px;
    line-height: initial;
    color: #666;
    display: block;
    min-width: 40px;
    height: 20px;
    padding: 1px 8px;
    margin: 6px 38px 6px 0;
    white-space: nowrap;
  }
}
</style>