<template>
  <div class="theme-show-wrapper position-absolute" @click.stop="">
    <h3>{{ title }}</h3>
    <ul class="theme-content clearfix">
      <li class="col-province float-left" v-for="i in colNum" :key="i">
        <span :class="{
            active : selProvince === name
        }" @click.stop="handleClick(name)" class="mt-item" v-for="name in colArr[i - 1]" :key="name">{{
          name
        }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: {
    dataList: Array,
    title: String,
    maxRow: {
      type: Number,
      default: 12,
    },
  },
  computed: {
    ...mapState("cityStore", ["selCitys", "selProvince"]),
    colNum() {
      // 多少列
      return Math.ceil(this.dataList.length / this.maxRow);
    },
    colArr() {
      var arr = [];
      for (var i = 0; i < this.colNum; i++) {
        if (i == this.colNum - 1) {
          arr.push(this.dataList.slice(i * this.maxRow));
        } else {
          arr.push(this.dataList.slice(i * this.maxRow, (i + 1) * this.maxRow));
        }
      }
      return arr;
    },
  },
  methods : {
      handleClick (name) {
          this.$store.commit("cityStore/setProvince",name);
          this.$emit("show",false);
      }
  }
};
</script>

<style lang="less" scoped>
.theme-show-wrapper {
  padding: 20px 0 20px 15px;
  line-height: initial;
  width: 264px;
  background-color: #fff;
  border: 1px solid #e5e5e5;
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  height: 375px;
  top : calc(100% + 5px);
  left: 20px;
  &::after {
    content: "";
    position: absolute;
    left: 26px;
    top : -4.6px;
    transform: rotate(45deg);
    width: 8px;
    height: 8px;
    background-color: #fff;
    border-left: 1px solid #e5e5e5;
    border-top: 1px solid #e5e5e5;
  }
  h3 {
    font-size: 16px;
    color: #ccc;
    margin-bottom: 11px;
    font-weight: normal;
  }
  .theme-content {
    .col-province {
      .mt-item {
        cursor: pointer;
        font-size: 12px;
        color: #666;
        display: block;
        min-width: 40px;
        height: 20px;
        padding: 1px 8px;
        margin: 6px 38px 6px 0;
        &:hover {
          border-radius: 10px;
          background: #f4f4f4;
        }
        &.active {
          background: linear-gradient(to bottom right, #ffd000, #ffbd00);
          border-radius: 10px;
          color: #222222;
        }
      }
    }
  }
}
</style>