<template>
  <div class="theme-show-wrapper position-absolute" @click.stop="">
    <h3>{{ title }}</h3>
    <div class="theme-content w-100 d-flex">
      <div class="col-province " v-for="i in colNum" :key="i">
        <span
          :class="{}"
          @click.stop="handleClick(item)"
          class="mt-item"
          v-for="item in colArr[i - 1]"
          :key="item.name"
          >{{ item.name }}</span
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  props: {
    dataList: Array,
    title: String,
    maxRow: {
      type: Number,
      default: 12,
    },
  },
  computed: {
    ...mapGetters("cityStore", ["computedCity"]),
    colNum() {
      // 多少列
      return Math.ceil(this.dataList.length / this.maxRow);
    },
    colArr() {
      var arr = [];

      for (var i = 0; i < this.colNum; i++) {
        if (i == this.colNum - 1) {
          arr.push(this.dataList.slice(i * this.maxRow));
        } else {
          arr.push(this.dataList.slice(i * this.maxRow, (i + 1) * this.maxRow));
        }
      }
      return arr;
    },
  },
  methods: {
    handleClick(item) {
      this.$store.commit("cityStore/setCity", item);
      this.$emit("show", false);
    },
  },
};
</script>

<style lang="less" scoped>
.theme-show-wrapper {
  padding: 20px 0 20px 15px;
  line-height: initial;
  background-color: #fff;
  border: 1px solid #e5e5e5;
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  min-width: 150px;
  height: 375px;
  top: calc(100% + 5px);
  left: 20px;
  &::after {
    content: "";
    position: absolute;
    left: 26px;
    top: -4.6px;
    transform: rotate(45deg);
    width: 8px;
    height: 8px;
    background-color: #fff;
    border-left: 1px solid #e5e5e5;
    border-top: 1px solid #e5e5e5;
  }
  h3 {
    font-size: 16px;
    color: #ccc;
    margin-bottom: 11px;
    font-weight: normal;
  }
  .theme-content {
    .col-province {
      .mt-item {
        cursor: pointer;
        font-size: 12px;
        color: #666;
        display: block;
        min-width: 40px;
        height: 20px;
        padding: 1px 8px;
        margin: 6px 38px 6px 0;
        white-space: nowrap;
      }
    }
  }
}
</style>